/* navbar animation */
.link{
	position: relative;
	display: flex;
	align-items: center;
	gap: 0.5rem;
	padding-left: 0.75rem;
	padding-right: 0.75rem;
	padding-top: 1rem;
	padding-bottom: 1rem;
	transition-property: background-color, border-color, color, fill, stroke;
	transition-timing-function: cubic-bezier(0.4, 0, 0.2, 1);
	transition-duration: 150ms;
	transition-delay: 200ms;
}
.link::before{
	content: "";
	position: absolute;
	content: "";
	top: 0px;
	content: "";
	left: 0px;
	content: "";
	height: 100%;
	content: "";
	width: 0.25rem;
	content: "";
	--tw-scale-y: 0;
	-webkit-transform: var(--tw-transform);
	        transform: var(--tw-transform);
	content: "";
	--tw-bg-opacity: 1;
	background-color: rgba(64, 49, 52, var(--tw-bg-opacity));
}
.link:hover{
	--tw-text-opacity: 1;
	color: rgba(255, 255, 255, var(--tw-text-opacity));
}
.link:hover::before{
	content: "";
	width: 100%;
	content: "";
	--tw-scale-y: 1;
	-webkit-transform: var(--tw-transform);
	        transform: var(--tw-transform);
}
.link:active::before{
	content: "";
	--tw-bg-opacity: 1;
	background-color: rgba(136, 102, 108, var(--tw-bg-opacity));
}
